import { defineStore } from "pinia";
import templates from '../localDatabase/templates.json'
import { Template } from "@/interfaces/Template";
import { TemplateBase } from "@/interfaces/TemplateBase";

export const userTemplatesStore = defineStore('userTemplates',  {
    state: () => ({
        templates: [] as Template[],
    }),
    actions: {
        createdTemplate(template: TemplateBase): boolean {
            //TODO(tolstov): метод для создания шаблона
            this.templates.push(template as Template)
            return true; //будущий ответ от сервера
        },
        editTemplate(template: Template) {
            //TODO(tolstov): метод для редактирования
            const index = this.templates.findIndex(item => item.id === template.id);

            if (index !== -1) {
                this.templates[index] = template;
            }
        },
        getTemplate() {
            //TODO(tolstov): метод для получения из базы и добавления их в основной массив
            templates.forEach((item) => {
                this.templates.push(item as Template)
            })
        },
        deleteTemplate(id: number) {
            //TODO(tolstov): метод для удаления объекта
            const result = this.templates.filter((t: Template) => t.id !== id);
            this.templates = result;
        }
    }
})