<template>
  <div class="app" v-if="route.name === 'not-found'">
    <main>
      <router-view />
    </main>
  </div>

  <div class="app" v-else>
    <vue-window-spinner :is-loading="loading" />
    <vue-header class="container" v-if="route.path !== '/registration'" />
    <main
      :class="{
        container: route.path !== '/registration',
        content: route.path !== '/registration',
      }"
    >
      <router-view />
    </main>
    <vue-footer class="container" v-if="route.path !== '/registration'" />
  </div>
</template>

<script setup lang="ts">
import VueFooter from "./components/Footer/VueFooter.vue";
import VueHeader from "./components/Header/VueHeader.vue";
import VueWindowSpinner from "./components/Spinners/VueWindowSpinner.vue";
import { useRouter, useRoute } from "vue-router";
import { provide, ref, watch } from "vue";
import { useUserStore } from "./store/user";

const router = useRouter();
const route = useRoute();
const userStore = useUserStore();

let loading = ref(true);

//TODO(tolstov): на время разработки время равно 0
setTimeout(() => {
  loading.value = false;
}, 0);

provide("userRole", userStore.userRoleInfo.userRole);

watch(
  () => route.path,
  (newPath: string) => {
    if (
      newPath === "/registration" &&
      userStore.userRoleInfo.userRole !== null
    ) {
      console.log(userStore.userRoleInfo);
      router.push("/");
    }
  },
  { immediate: true }
);
</script>

<style lang="scss">
@import url(../src/styles/fonts.css);
@import url(../src/styles/variable.css);
@import url(../src/styles/main.css);

.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.content {
  flex: 1;
  margin-bottom: 10% !important;
  padding-bottom: 50px !important;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  @media (max-width: 860px) {
    max-height: 80vh;
    overflow: scroll;
  }
}

@media (max-width: 860px) {
  .footer {
    display: none !important;
  }
}
</style>
