import { defineStore } from "pinia";
import { reactive } from "vue";
import { useUserStore } from "./user";

export interface Tab {
  id: number;
  title: string;
  isActive: boolean;
}

const userCustomerTabsLK: Tab[] = [
  {
    id: 3,
    title: "Завершенные заявки",
    isActive: true,
  },
  {
    id: 6,
    title: "Статистика",
    isActive: true,
  },
  {
    id: 7,
    title: "Создать заказ",
    isActive: true,
  },
  {
    id: 8,
    title: "Создать шаблон",
    isActive: true,
  },
];

const userCustomerTabsMenu: Tab[] = [
  {
    id: 1,
    title: "О нас",
    isActive: true,
  },
  {
    id: 2,
    title: "Оферта",
    isActive: true,
  },
];

const userPerformerTabsLK: Tab[] = [
  {
    id: 3,
    title: "Завершенные заявки",
    isActive: true,
  },
  {
    id: 6,
    title: "Статистика",
    isActive: true,
  },
];

const userPerformerTabsMenu: Tab[] = [
  {
    id: 1,
    title: "О нас",
    isActive: true,
  },
  {
    id: 2,
    title: "Оферта",
    isActive: true,
  },
];

export const useSettingsStore = defineStore("userSettings", {
  state: () => ({
    isNotificationTelegram: true,
    tabsLK: reactive<Tab[]>([]),
    tabsMenu: reactive<Tab[]>([]),
  }),
  actions: {
    initNotificationsTelegram() {
      this.isNotificationTelegram = true;
    },
    
    initUserTabs(userId: number) {
      this.loadTabsFromLocalStorage(userId);
      this.initTabsMenu(userId);
    },
    
    initTabsMenu(userId: number) {      
      if (userId === 0) {
        // Для роли "customer"
        if (this.tabsMenu.length === 0) {
          this.tabsMenu = [...userCustomerTabsMenu, ...userCustomerTabsLK];
          this.saveTabsToLocalStorage(userId);
        }
      } else if (userId === 1) {
        // Для роли "performer"
        if (this.tabsMenu.length === 0) {
          this.tabsMenu = [...userPerformerTabsMenu, ...userPerformerTabsLK];
          this.saveTabsToLocalStorage(userId);
        }
      }
    },

    // Загрузка вкладок из localStorage
    loadTabsFromLocalStorage(userId: number) {
      const roleKey = userId === 0 ? "customer" : "performer";
      const storedTabsLK = localStorage.getItem(`userTabLK_${roleKey}`);
      const storedTabsMenu = localStorage.getItem(`userTabMenu_${roleKey}`);

      if (storedTabsLK) {
        this.tabsLK = reactive(JSON.parse(storedTabsLK));
      } else {
        // Если вкладок LK нет, инициализируем их
        this.tabsLK = userId === 0 ? [...userCustomerTabsLK] : [...userPerformerTabsLK];
        this.saveTabsToLocalStorage(userId);
      }

      if (storedTabsMenu) {
        this.tabsMenu = reactive(JSON.parse(storedTabsMenu));
      } else {
        // Если вкладок меню нет, инициализируем их
        this.tabsMenu = userId === 0 ? [...userCustomerTabsMenu] : [...userPerformerTabsMenu];
        this.saveTabsToLocalStorage(userId);
      }
    },

    // Сохранение вкладок в localStorage
    saveTabsToLocalStorage(userId: number) {
      const roleKey = userId === 0 ? "customer" : "performer";
      localStorage.setItem(`userTabLK_${roleKey}`, JSON.stringify(this.tabsLK));
      localStorage.setItem(`userTabMenu_${roleKey}`, JSON.stringify(this.tabsMenu));
    },

    // Обновление состояния вкладки LK
    updateTabLKStatus(tabId: number, isActive: boolean) {
      const tab = this.tabsLK.find((t) => t.id === tabId);
      if (tab) {
        tab.isActive = isActive;
        this.saveTabsToLocalStorage(this.getCurrentUserId());
      }
    },

    // Обновление состояния вкладки меню
    updateTabMenuStatus(tabId: number, isActive: boolean) {
      const tab = this.tabsMenu.find((t) => t.id === tabId);
      if (tab) {
        tab.isActive = isActive;
        this.saveTabsToLocalStorage(this.getCurrentUserId());
      }
    },
    
    getCurrentUserId(): number {
      const userStore = useUserStore(); 
      return userStore.userRoleInfo.userRole!; 
    },
  },
});