import { defineStore } from "pinia";
import { useUserStore } from "./user";
import { userTemplatesStore } from "./template";
import { useSettingsStore } from "./settings";

export const usePinia = defineStore('initPinia',  {
    state: ()  => ({
        userStore: useUserStore(),
        userTemplates: userTemplatesStore(),
        userSettings: useSettingsStore(),
    }),
    actions: {
        initStore() {
            const userRole = this.userStore.getUserInfoByPhone()
            this.userTemplates.getTemplate();            
            if(userRole === 0 || userRole === 1) {
                this.userSettings.initUserTabs(userRole);
            }
        }
    }
})