<template>
  <nav class="menu">
    <div
      class="menu__item"
      v-for="item of menuItems"
      :key="item.id"
      :class="{ menu__item__active: item.active === true }"
      @click="setActive(item)"
    >
      <p class="menu__item--p">{{ item.title }}</p>
      <img :src="item.icon" alt="" />
    </div>
  </nav>
</template>

<script setup>
import { inject, ref, watch } from "vue";
import { useRouter, useRoute } from "vue-router";
import verification_icon from "../../assets/icons/verification.svg";
import offer_icon from "../../assets/icons/document.svg";
import add_icon from "../../assets/icons/add.svg";
import select_icon from "../../assets/icons/select.svg";
import price from "../../assets/icons/price.svg";
import contacts from "../../assets/icons/contacts.svg";
import user from "../../assets/icons/user.svg";
import { useSettingsStore } from "@/store/settings";

const settingStore = useSettingsStore();
const userRole = inject("userRole");

const router = useRouter();
const route = useRoute();

const menuItems = ref([]);

const baseMenuItems = [
  {
    id: 1,
    title: "О нас",
    route: "/",
    icon: verification_icon,
    active: false,
  },
  {
    id: 2,
    title: "Оферта",
    route: "/offer",
    icon: offer_icon,
    active: false,
  },
  {
    id: 6,
    title: "Контакты",
    route: "/contacts",
    icon: contacts,
    active: false,
  },
  {
    id: 7,
    title: "Цены",
    route: "/price",
    icon: price,
    active: false,
  },
];

const itemsMenuCustomer = [
  {
    id: 3,
    title: "Оформить заявку",
    route: "/place-order",
    icon: select_icon,
    active: false,
  },
  {
    id: 4,
    title: "Профиль",
    route: "/profile",
    icon: user,
    active: false,
  },
];

const itemsMenuPerformer = [
  {
    id: 3,
    title: "Взять заказ",
    route: "/take-order",
    icon: add_icon,
    active: false,
  },
  {
    id: 4,
    title: "Профиль",
    route: "/profile",
    icon: user,
    active: false,
  },
];

const noneAuthorizedMenu = [
  itemsMenuPerformer[0],
  itemsMenuCustomer[0],
  {
    id: 5,
    title: "Авторизация",
    route: "/registration",
    icon: user,
    active: false,
  },
];

const updateActiveItem = () => {
  menuItems.value = menuItems.value.map((item) => ({
    ...item,
    active: item.route === route.path,
  }));
};

const updateMenuItems = () => {
  let additionalItems = [];
  if (userRole === null) {
    additionalItems = noneAuthorizedMenu;
  } else if (userRole === 0) {
    additionalItems = itemsMenuCustomer;
  } else if (userRole === 1) {
    additionalItems = itemsMenuPerformer;
  }

  menuItems.value = [...baseMenuItems, ...additionalItems];
  updateActiveItem();
};

updateMenuItems();

watch(
  () => route.path,
  () => {
    updateActiveItem();
  }
);

watch(
  () => settingStore.tabsMenu,
  (newTabsMenu) => {
    newTabsMenu.forEach((tab) => {
      if (!tab.isActive) {
        menuItems.value = menuItems.value.filter((item) => item.id !== tab.id);
      } else {
        const itemExists = menuItems.value.some((item) => item.id === tab.id);
        if (!itemExists) {
          const itemToAdd = [
            ...baseMenuItems,
            ...itemsMenuCustomer,
            ...itemsMenuPerformer,
            ...noneAuthorizedMenu,
          ].find((item) => item.id === tab.id);
          if (itemToAdd) {
            menuItems.value.push(itemToAdd);
            menuItems.value.sort((a, b) => a.id - b.id);
          }
        }
      }
    });
  },
  { deep: true, immediate: true }
);

const setActive = (item) => {
  router.push(item.route);
};
</script>

<style lang="scss">
@import url(../../styles/variable.css);

.menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  background: var(--yellow);
  border-radius: 20px;
  border-bottom-left-radius: 0px;

  @media (max-width: 860px) {
    // flex-direction: column;
    // justify-content: start;
    // align-items: start;
    // border-bottom-right-radius: 0px;
    position: fixed;
    bottom: 10px;
    left: 0;
    right: 0;
    width: calc(100% - 20px);
    margin: 0 auto;
    z-index: 100;
    box-shadow: 0px 0px 20px 5px #0000002b;
  }

  &__item {
    cursor: pointer;
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: center;
    padding: 5px 15px;
    border-radius: 10px;
    transition: all 0.3s ease, transform 0.3s ease;
    animation: showMenuItem 0.5s ease;
    @media (max-width: 460px) {
      padding: 5px;
    }
    @keyframes showMenuItem {
      0% {
        opacity: 0;
        scale: 0;
      }
      100% {
        opacity: 1;
        scale: 1;
      }
    }

    &--p {
      @media (max-width: 1000px) {
        display: none;
      }
      font-size: 25px;
      margin-left: 6px;
      text-wrap: nowrap;
    }

    & img {
      display: block;
      width: 20px;
      @media (max-width: 1000px) {
        width: 30px;
      }
    }

    &__active {
      background: var(--white);
      transform: scale(1.05);
      @media (max-width: 860px) {
        transform: scale(1);
      }
    }
  }
}
</style>
