import { createRouter, createWebHistory, RouteRecordRaw } from 'vue-router'
import { useUserStore } from '@/store/user';

declare module 'vue-router' {
  interface RouteMeta {
    title?: string;
    requiresAuth?: boolean;
    allowedRoles?: number[]; // Явно указываем тип
  }
}

const routes: RouteRecordRaw[] = [
  {
    path: '/',
    name: 'home',
    component: () => import('../pages/HomePage.vue'),
    meta: {
      title: 'Главная'
    }    
  },  
  {
    path: '/registration',
    name: 'registration',
    component: () => import('../pages/RegistrationPage.vue'),
    meta: {
      title: 'Регистрация',
      requiresAuth: false,
      allowedOnlyFor: null
    },
  },
  {
    path: '/offer',
    name: 'offer',
    component: () => import('../pages/OfferPage.vue'),
    meta: {
      title: 'Оферта'
    }
  },
  {
    path: "/take-order",
    name: "take-order",
    component: () => import("../pages/TakeOrderPage.vue"),
    meta: {
      title: 'Взять заказ',
    },
  },
  {
    path: "/place-order",
    name: "place-order",
    component: () => import("../pages/PlaceOrderPage.vue"),
    meta: {
      title: 'Оформить заявку',
    },
  },
  {
    path: "/take-order/:id",
    name: "take-order-details",
    component: () => import("../pages/OrderDetailsPage.vue"),
    meta: {
      title: 'Детали заказа',
    },
  },
  {
    path: "/profile",
    name: "profile",
    component: () => import("../pages/ProfilePage.vue"),
    meta: {
      title: 'Профиль',
      requiresAuth: true,
      allowedRoles: [0, 1]
    },
  },
  {
    path: "/contacts",
    name: "contacts",
    component: () => import("../pages/ContactsPage.vue"),
    meta: {
      title: 'Контакты',
      requiresAuth: false,      
    },
  },
  {
    path: "/price",
    name: "price",
    component: () => import("../pages/PricePage.vue"),
    meta: {
      title: 'Цены',
      requiresAuth: false,      
    },
  },
  {
    path: '/:pathMatch(.*)*',
    name: 'not-found',
    component: () => import('../pages/404.vue'),
    meta: {
      title: '404 | Страница не найдена',
    },
  },
]

const router = createRouter({
  history: createWebHistory(),
  routes
})

router.beforeEach(async (to, from, next) => {
  const userStore = useUserStore();
  document.title = String(to.meta.title || 'arbait');
  
  const userRole = userStore.userRoleInfo.userRole;
  
  if (to.meta.allowedOnlyFor === null) {
    if (userRole !== null) {
      next('/');
      return;
    }
    next();
    return;
  }
  
  if (to.meta.requiresAuth) {
    if (userRole === null) {
      next('/');
      return;
    }
    
    if (to.meta.allowedRoles && !to.meta.allowedRoles.includes(userRole)) {
      next('/not-found');
      return;
    }
  }

  next();
});


export default router;
