<template>
  <div class="spinner-container" v-if="isLoading">
    <div class="spinner big-spinner">
      <v-progress-circular color="white" indeterminate :size="69" />
    </div>
  </div>
</template>

<script lang="ts" setup>
import { defineProps, watch } from "vue";

const props = defineProps({
  isLoading: {
    type: Boolean,
    required: true,
    default: false,
  },
});

// Функция для блокировки скролла
const disableScroll = () => {
  document.body.style.overflow = "hidden";
};

// Функция для разблокировки скролла
const enableScroll = () => {
  document.body.style.overflow = "auto";
};

watch(
  () => props.isLoading,
  (newValue) => {
    if (newValue) {
      disableScroll();
    } else {
      enableScroll();
    }
  }
);
</script>

<style lang="scss" scoped>
@import "vuetify/styles";
@import "vue3-toastify/dist/index.css";
.spinner {
  &-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background: #1a19197b;
    transition: opacity 0.4s ease;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 10000;
  }
}
</style>
